body {
  overflow-x: hidden;
  background-color: #F4F9F9;
}
body header.top-navigation .dropdown-menu {
  margin-top: 1rem;
}
body header.top-navigation .search-navbar .select2-selection .select2-selection--single {
  border-radius: 16px;
}
body header.top-navigation .search-navbar .select2-container--krajee-bs5 .select2-selection {
  background-color: transparent;
  border-radius: 16px;
}
body header.top-navigation .search-navbar .select2-container--krajee-bs5 .select2-selection--single .select2-selection__rendered {
  color: #212529;
  padding-left: 8px;
  padding-right: 8px;
}
body header.top-navigation .navbar-toggler {
  border: none;
}
body main {
  padding-top: 56px;
  /*
  .grid-view {
    div.table-responsive {
      min-height: 420px;
    }
  }*/
}
body main aside.left-navigation {
  z-index: 4;
  height: 100%;
  position: fixed;
  top: 0;
  bottom: 0;
  padding: 62px 0 0;
  transition: all 0.25s;
  overflow-y: auto;
}
body main aside.left-navigation ul {
  padding: 0.5em 0.5em 0;
}
body main aside.left-navigation ul li {
  list-style: none;
  line-height: 2.5rem;
  margin-bottom: 0.25rem;
}
body main aside.left-navigation ul li:hover {
  color: #10A19D;
  border-radius: 8px;
}
body main aside.left-navigation ul li a {
  display: block;
  text-decoration: none;
  color: #000;
}
body main aside.left-navigation ul li.active a.active {
  border-radius: 8px;
  background-color: #540375;
  color: #fff;
}
body main section.content {
  padding: 1rem 1rem 0 1rem;
  margin-left: 206px;
  transition: all 0.1s;
}
body main .card {
  border-radius: 0;
}
body main .card .kv-panel-before {
  border-bottom: none;
}
body main .card .card-header {
  border: none;
}
body main .card .card-footer {
  border-top: none;
  background: transparent;
  padding: 0;
}
body main .card table.inside-border {
  border-collapse: collapse;
  margin-bottom: 0;
}
body main .card table.inside-border td, body main .card table.inside-border th {
  border: 1px solid #ced4da;
}
body main .card table.inside-border tr {
  border: 0;
}
body main .card table.inside-border tr:first-child th {
  border-top: 1px solid #ced4da;
}
body main .card table.inside-border tr:last-child td {
  border-bottom: 0;
}
body main .card table.inside-border tr td:first-child,
body main .card table.inside-border tr th:first-child {
  border-left: 0;
}
body main .card table.inside-border tr td:last-child,
body main .card table.inside-border tr th:last-child {
  border-right: 0;
}
body main .card table.inside-border.table-detail-view th, body main .card table.inside-border.table-detail-view td {
  border: 1px solid #ced4da;
}
body main .card table.inside-border.table-detail-view tr {
  border: 0;
}
body main .card table.inside-border.table-detail-view tr:last-child th, body main .card table.inside-border.table-detail-view tr:last-child td {
  border-bottom: 0;
}
body main table td.column-barang {
  width: 316px !important;
  max-width: 316px !important;
}
body main table td.column-barang.select2-container {
  width: 300px !important;
  max-width: 300px !important;
}
body main table td.column-vendor {
  width: 216px !important;
  max-width: 216px !important;
}
body main table td.column-vendor.select2-container {
  width: 200px !important;
  max-width: 200px !important;
}
body main table.table-fixes-last-column tr:not(.kv-expand-detail-row) th:last-child,
body main table.table-fixes-last-column tr:not(.kv-expand-detail-row) td:last-child {
  background-color: #F4F9F9;
  border-right: 2px solid #dee2e6;
  border-bottom: 2px solid #dee2e6;
  position: sticky;
  right: 0;
  z-index: 2;
}
body main table.table-fixes-last-column thead tr.kv-expand-detail-row table tr th:last-child,
body main table.table-fixes-last-column thead tr.kv-expand-detail-row table tr td:last-child,
body main table.table-fixes-last-column tbody tr.kv-expand-detail-row table tr th:last-child,
body main table.table-fixes-last-column tbody tr.kv-expand-detail-row table tr td:last-child {
  background-color: #fff;
  position: static;
  border-top: none;
  border-bottom: none;
  border-right: 1.45px solid #dee2e6;
}
body main ul.pagination {
  margin: 0 0 auto;
}
body main .kv-table-footer {
  background: transparent;
}
body main div.modal h5.modal-title {
  font-size: 1.25rem;
}
body footer {
  padding: 0.75rem 1rem;
  margin-left: 200px;
  border-top: 1px solid #e9ecef;
}

/* all screen include ipad mini */
@media only screen and (min-width: 768px) {
  body main aside.left-navigation {
    width: 206px;
    min-height: 100vh;
    z-index: 1;
    border-right: 1px solid #e9ecef;
  }
}
/* Mainstream tablet */
@media (max-width: 767px) {
  body main aside.left-navigation {
    width: 0;
    min-height: 100vh;
    z-index: 1;
    border-right: none;
  }
  body main section.content {
    margin-left: 0;
  }
  body footer {
    margin-left: 0;
  }
}
body.dark {
  background-image: none;
  background-color: #252525 !important;
  color: #fff !important;
}

body.dark main aside.left-navigation {
  border-right: none;
  background: #343a40;
  box-shadow: -5px -5px 10px #2f343a, 5px 5px 10px #394046;
}

body.dark input.form-control,
body.dark select.form-control,
body.dark textarea,
body.dark table,
body.dark table th,
body.dark table td,
body.dark div.card,
body.dark aside,
body.dark nav.card,
body.dark ul.pagination li.page-item a.page-link,
body.dark footer,
body.dark div.modal-content,
body.dark div.list-group,
body.dark a.list-group-item {
  background-color: #252525 !important;
  color: #fff !important;
}

body.dark div.card,
body.dark div.modal-content,
body.dark a.list-group-item {
  border: 1px solid #fff !important;
}

body.dark .dropdown-menu {
  border: 1px solid #fff !important;
  background-color: #252525 !important;
  color: #fff !important;
}

body.dark a, body.dark i.bi {
  color: #fff !important;
}

body.dark .dropdown-item:hover {
  background-color: #540375 !important;
}

body.dark ul.pagination li.page-item.active a.page-link {
  color: #212529 !important;
  background-color: #fff !important;
  border-color: transparent;
}

body.dark footer {
  border-top: 0;
}

@media screen {
  body {
    display: none !important;
  }
}
@media print {
  * {
    font-family: "Arial", serif;
    font-weight: normal;
    box-sizing: border-box;
  }
  @page {
    size: A4;
    margin: 12pt 12pt 24pt 24pt;
  }
  /* @page :first {
     size: A4;
     margin: 12pt 12pt 24pt 24pt;
   }*/
  body {
    margin: 0;
    padding: 0;
    font-size: 11pt;
    counter-reset: page;
    /* div#header {
       position: fixed;
       top: 0;
       left: 0;
       width: 100%;
       counter-increment: page;
     }

     div#content {
       padding-top: 36pt;

       .content-section {
         padding-top: 36pt;
       }

       .content-section:first-child {
         padding-top: 0;
       }
     }*/
  }
  body #page-number:after {
    content: counter(page);
  }
  body .page-break {
    page-break-before: always;
  }
  table {
    page-break-inside: auto;
    /* This is the key */
  }
  table tr {
    page-break-inside: avoid;
    page-break-after: auto;
  }
  table thead {
    display: table-header-group;
  }
  table tfoot {
    display: table-footer-group;
  }
  table.table {
    margin: 0;
    width: 99.9%;
    border-collapse: collapse;
    font-size: 8.5pt;
    border-left: 0.1em solid #252525;
    border-right: 0.1em solid #252525;
    border-top: 0.1em solid #252525;
    border-bottom: 0.1em solid #252525;
  }
  table.table th {
    text-align: center;
    font-weight: bold;
  }
  table.table td {
    vertical-align: top;
  }
  table.table th, table.table td {
    padding: 1pt 2pt;
  }
  table.table th.border-0, table.table td.border-0 {
    border: 0 !important;
  }
  table.table th.border-start-0, table.table td.border-start-0 {
    border-left: 0 !important;
  }
  table.table th.border-end-0, table.table td.border-end-0 {
    border-right: 0 !important;
  }
  table.table th.border-end-1, table.table td.border-end-1 {
    border-right: 0.1em solid #252525 !important;
  }
  table.table th.border-top-0, table.table td.border-top-0 {
    border-top: 0 !important;
  }
  table.table th.border-top-1, table.table td.border-top-1 {
    border-top: 0.1em solid #252525 !important;
  }
  table.table th.border-bottom-0, table.table td.border-bottom-0 {
    border-bottom: 0 !important;
  }
  table.table th.border-bottom-1, table.table td.border-bottom-1 {
    border-bottom: 0.1em solid #252525 !important;
  }
  table.table.table-bordered th, table.table.table-bordered td {
    border-left: 0.1em solid #252525;
    border-right: 0;
    border-top: 0.1em solid #252525;
    border-bottom: 0;
    border-collapse: collapse;
  }
  table.table.table-detail-view {
    width: 50%;
  }
  table.table.table-borderless {
    border: none;
  }
  table.table.table-borderless tr, table.table.table-borderless td, table.table.table-borderless th {
    border: none;
  }
  .card {
    margin-top: 6pt;
    padding: 6pt 0;
  }
  .card .table th, .card .table td {
    padding: 5pt 0;
    border: none;
  }
  .card .table.table-grid-view th, .card .table.table-grid-view td {
    padding: 5pt;
    border: 1pt solid #000;
  }
  .card .table.table-grid-view thead tr th {
    text-align: center;
  }
  .text-start {
    text-align: left !important;
  }
  .text-center {
    text-align: center !important;
  }
  .text-end {
    text-align: right !important;
  }
  .border-bottom {
    border: 1px !important;
  }
  .border-0 {
    border: 0 !important;
  }
  .border-start-0 {
    border-left: 0 !important;
  }
  .border-end-0 {
    border-right: 0 !important;
  }
  .border-top-0 {
    border-top: 0 !important;
  }
  .border-bottom-0 {
    border-bottom: 0 !important;
  }
  .border-1 {
    border: 0.1em solid black !important;
  }
  .p-0 {
    padding: 0;
  }
  .pb-2 {
    padding-bottom: 1rem !important;
  }
  .pb-3 {
    padding-bottom: 1.5rem !important;
  }
  .m-0 {
    margin: 0;
  }
  .m-3 {
    margin: 1.5rem !important;
  }
  .m-5 {
    margin: 2.5rem !important;
  }
  .mt-1 {
    margin-top: 1rem !important;
  }
  .mt-2 {
    margin-top: 1.5rem !important;
  }
  .mt-3 {
    margin-top: 1.5rem !important;
  }
  .mt-5 {
    margin-bottom: 2.5rem !important;
  }
  .mb-1 {
    margin-bottom: 1rem !important;
  }
  .mb-2 {
    margin-bottom: 1.5rem !important;
  }
  .mb-3 {
    margin-bottom: 1.5rem !important;
  }
  .mb-5 {
    margin-bottom: 2.5rem !important;
  }
  .ms-1 {
    margin-left: 0.5rem !important;
  }
  .me-1 {
    margin-right: 0.5rem !important;
  }
  .font-weight-bold {
    font-weight: bold;
  }
  .text-nowrap {
    white-space: nowrap;
  }
  span.badge {
    font-size: large;
    font-style: normal;
    font-weight: bold;
  }
  span.badge.bg-danger {
    color: #CF0A0A;
  }
  span.badge.bg-primary {
    color: #540375;
  }
  span.badge.bg-success {
    color: #FFBF00;
  }
  span.badge.bg-warning {
    color: #FF7000;
  }
  span.badge.bg-info {
